import React from "react";
import DetailPage from "./DetailPage";
import FullListData from "../Internship/FullListData";
import { useParams } from "react-router-dom";

const MainDetail = () => {
  const { Jid, Jtitle } = useParams();
  const KuchToh = (props) => {
    if (Jid < 7 && Jtitle === props.identifier["name"]) {
      return (
        <>
          <DetailPage id={Jid} title={Jtitle} />
        </>
      );
    } else {
      return (
        <>
          <p>No Data Found</p>
        </>
      );
    }
  };

  return <>{KuchToh(FullListData[Jid])}</>;
};

export default MainDetail;
