import React from "react";

const widthFit = {
  width: "fit-content",
};

const Modal = (prop) => {
  console.log(prop);
  return (
    <>
      {/* <button type="button" class="btn btn-primary" data-toggle="modal" data-target="#SignPopUp">
  Launch static backdrop modal
</button> */}

      <div
        class="modal fade"
        id="SignPopUp"
        data-backdrop="static"
        data-keyboard="false"
        tabindex="-1"
        aria-labelledby="SignPopUpLabel"
        aria-hidden="true"
      >
        <div class="modal-dialog modal-dialog-centered">
          <div class="modal-content">
            <button
              type="button"
              class="close"
              data-dismiss="modal"
              aria-label="Close"
            >
              <span aria-hidden="true">&times;</span>
              <p>Close Tab</p>
            </button>
            <div class="modal-body text-center">
              <div className="modal-text">
                <p>Join Us At Bagcampus, Get Notified With Best Opportunities <span>Ad-Free</span> </p>
              </div>
              <div class="button GooglePartner" id="SignIn">
                <button
                  class="btn ml-1 btn-google mr-md-3 mb-2 mb-md-0"
                  id="GooglePartnerlogin"
                  style={widthFit}
                >
                  <i class="fab fa-google mr-1"></i> Sign In With Google
                </button>
              </div>
              <a
                href={prop.ApplyLink}
                target="_blank"
                rel="noopener noreferrer"
              >
                Skip For Now - Move Forward To Apply Job
              </a>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

const SignInPopUp = (props) => {
  return <>{Modal(props.link)}</>;
};

export default SignInPopUp;
