import React from "react";
import Footer from "../Footer/Footer";
import Nav from "../Nav/Nav";
import FullListData from "../Internship/FullListData";
import "./DetailPage.css";
import SignInPopUp from "./SignInPopUp";

const HeaderPage = (props) => {
  return (
    <>
      <div class="page-header">
        <div class="container">
          <div class="row">
            <div class="col-lg-10 col-md-6 col-xs-12">
              <div class="breadcrumb-wrapper row">
                <div class="img-wrapper mt-sm-4 ml-3 col-lg-3">
                  <img
                    src={props.hiringOrganization["logo"]}
                    className="img-fluid m-4"
                    alt=""
                  />
                </div>
                <div class="content ml-3 mt-3 col-lg-8">
                  <h3 class="product-title">Hiring {props.title}</h3>
                  <p class="brand">{props.hiringOrganization["name"]}</p>
                  <div class="tags">
                    <span>
                      <i class="lni-map-marker"></i> {props.Location}
                    </span>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </div>
    </>
  );
};

const Description = (props) => {
  const MyLinkP =
    "https://bagcampus.in/#/detail/" +
    props.identifier["name"] +
    "/" +
    props.id;

  const refJL = props.jobLocation["address"];

  const ShareText =
    "Check%20This:%20%0A%0A" +
    props.hiringOrganization["name"] +
    "%20Hiring%20" +
    props.title +
    "%20at%20" +
    props.Location +
    "%0A%0ARead%20More:%20%0A" +
    MyLinkP +
    "%0A%0AJoin%20Us%20With%20Bagcampus%20To%20Unlock%20Great%20Opportunity.";

  const twitterShareLink = "https://twitter.com/intent/tweet?text=" + ShareText;
  
  const WhatsappShareLink =
    "https://api.whatsapp.com/send?text=" +
    "Check%20This:%20%0A%0A" +
    props.hiringOrganization["name"] +
    "%20Hiring%20" +
    props.title +
    "%20at%20" +
    props.Location +
    "%0A%0ARead%20More:%20%0A" +
    "https://bagcampus.in/%23/detail/" +
    props.identifier["name"] +
    "/" +
    props.id +
    "%0A%0AJoin%20Us%20With%20Bagcampus%20To%20Unlock%20Great%20Opportunity.";

  const LinkedinShareLink = "https://www.linkedin.com/shareArticle?mini=true&url=" + MyLinkP;
  const FBShareLink = "https://www.facebook.com/sharer/sharer.php?u=" + MyLinkP; 

  return (
    <>
      <section class="job-detail section">
        <div class="container">
          <div class="row justify-content-between">
            <div class="col-lg-8 col-md-12 col-xs-12">
              <div class="content-area">
                <h4>Job Description</h4>
                <p>{props.description}</p>
                <h5>How To Apply</h5>
                <p>Click On Link Below To Apply For Internship</p>
                <h5>Type</h5>
                <p>{props.type}</p>
                <a
                  href="/#"
                  data-toggle="modal"
                  data-target="#SignPopUp"
                  target="__blank"
                  class="btn btn-common"
                >
                  Apply job
                </a>
              </div>
            </div>
            <div class="col-lg-4 col-md-12 col-xs-12">
              <div class="sideber">
                <div class="widghet">
                  <h3>Office Location</h3>
                  <div className="location-container">
                    <p>
                      <b>{props.hiringOrganization["name"]}</b>
                    </p>
                    <p>{refJL["streetAddress"]}</p>
                    <p>{refJL["addressLocality"]}</p>
                    <p>State: {refJL["addressRegion"]}</p>
                    <p>Pin: {refJL["postalCode"]}</p>
                  </div>
                </div>
                <div class="widghet">
                  <h3>Share This Job</h3>
                  <div class="share-job">
                    <form method="post" class="subscribe-form">
                      <div class="form-group">
                        <input
                          type="link"
                          name="link"
                          class="form-control"
                          placeholder="https://joburl.com"
                          required=""
                          value={MyLinkP}
                        />
                        <button
                          type="button"
                          name="subscribe"
                          class="btn btn-common sub-btn"
                          data-toggle="tooltip"
                          data-placement="right"
                          title="Click To Copy Link"
                        >
                          <i class="fas fa-copy"></i>
                        </button>

                        <div class="clearfix"></div>
                      </div>
                    </form>
                    <ul class="mt-1 footer-social pl-3">
                      <li>
                        <a
                          class="whatsapp"
                          href={WhatsappShareLink}
                          target="__blank"
                        >
                          <i class="fab fa-whatsapp"></i>
                        </a>
                      </li>
                      <li>
                        <a class="linkedin" href={LinkedinShareLink} target="__blank">
                          <i class="fab fa-linkedin"></i>
                        </a>
                      </li>
                      <li>
                        <a class="facebook" href={FBShareLink} target="__blank">
                          <i class="fab fa-facebook"></i>
                        </a>
                      </li>
                      <li>
                        <a
                          class="twitter"
                          href={twitterShareLink}
                          target="__blank"
                        >
                          <i class="fab fa-twitter"></i>
                        </a>
                      </li>
                    </ul>
                  </div>
                </div>
              </div>
            </div>
          </div>
        </div>
      </section>
    </>
  );
};

const DetailPage = (props) => {
  return (
    <>
      <section id="DetailPage">
        <Nav />
        {HeaderPage(FullListData[props.id])}
        {Description(FullListData[props.id])}
        <Footer />
        <SignInPopUp link={FullListData[props.id]} />
      </section>
    </>
  );
};

export default DetailPage;
